import {
  showField,
  hideField
} from '../../components/form-util/visibility'

import {
  isCpfValid,
  formatCpf
} from '../utils'

const FormUtilConfig = {
  container: '.form-group',
  hideClass: 'u-hidden'
}

const RequestEmployee = function () {
  const $refs = {
    contractType: document.querySelector('[xname=inptipoDeContrato]'),
    cpf: document.querySelector('[xname=inpcolaboradorCpf]')
  }

  const fields = {
    defaults: [
      'colaboradorNome',
      'colaboradorSexo',
      'colaboradorDataNascimento',
      'colaboradorCargo'
    ],
    native: [
      'colaboradorCpf',
      'colaboradorCtps',
      'colaboradorCtpsSerie',
      'colaboradorCtpsExpedicao',
      'colaboradorCtpsUf',
      'colaboradorCtpsPis',
      'colaboradorCptsAdmissao'
    ],
    foreign: [
      'colaboradorRne',
      'colaboradorNacionalidade',
      'colaboradorDataChegadaBrasil',
      'colaboradorTipoVisto',
      'colaboradorVistoValidade'
    ]
  }

  return {
    mount
  }

  /**
   * ðŸ”‘ Public Methods
   */
  function mount () {
    handleEmployeeFields($refs.contractType.value)
    validateCpf($refs.cpf.value)

    addTriggers()
  }

  /**
   * ðŸ”’ Private Methods
   */
  function addTriggers () {
    $refs.contractType
      .addEventListener(
        'change',
        () => handleEmployeeFields($refs.contractType.value)
      )

    $refs.cpf
      .addEventListener(
        'change',
        () => validateCpf($refs.cpf.value)
      )
  }

  function handleEmployeeFields (type) {
    return type === 'Estrangeiro'
      ? showForeignForm()
      : showNativeForm()
  }

  function showForeignForm () {
    fields.foreign
      .forEach(fieldId => showField(fieldId, FormUtilConfig))

    fields.native
      .forEach(fieldId => hideField(fieldId, FormUtilConfig))
  }

  function showNativeForm () {
    fields.native
      .forEach(fieldId => showField(fieldId, FormUtilConfig))

    fields.foreign
      .forEach(fieldId => hideField(fieldId, FormUtilConfig))

    // @todo preencher como brasileiro no campo oculto
  }

  function validateCpf (cpf) {
    if (cpf === '') {
      return
    }

    if (!isCpfValid(cpf)) {
      return alert(`O CPF informado nÃ£o possui um formato vÃ¡lido.\n${formatCpf(cpf)}`)
    }
  }
}

export default RequestEmployee()
