const defaults = {
  container: 'tr',
  hideClass: 'hidden',
  dataAttrRequired: 'data-was-required',
  requiredClass: 'execute-required'
}

/**
 * ðŸ’¡ Helpers
 */

function getFieldById (fieldId, options) {
  options = {
    returnArray: false,
    ...options
  }

  const fields = document.querySelectorAll(`[xname="inp${fieldId}"]`)

  if (options.returnArray) {
    return [...fields]
  }

  return fields.length > 1
    ? [...fields]
    : fields[0]
}

function getTable (table) {
  if (table instanceof HTMLElement) {
    return table
  }
  if (typeof table === 'string') {
    return document.querySelector(table)
  }

  return null
}

function clearFileField (field) {
  const id = field.getAttribute('xname').substring(3)
  const deleteBtn = field.parentElement
    .querySelector(`#div${id} > a:last-of-type`)

  if (deleteBtn) {
    deleteBtn.click()
  }
}

function hasFieldRequired (fields) {
  return fields.filter(
    field =>
      field.getAttribute(defaults.dataAttrRequired) ||
      field.getAttribute('required') === 'S'
  ).length > 0
}

/**
 * ðŸ‘€ Form Info
 */

export function getTaskAlias () {
  const taskAlias = document.querySelector('#inpDsFlowElementAlias')

  return taskAlias
    ? taskAlias.value
    : null
}

/**
 * ðŸ§· Form Utils
 */

export function getField (field, options) {
  options = {
    returnArray: false,
    ...options
  }

  if (field instanceof HTMLElement) {
    return options.returnArray
      ? [field]
      : field
  }

  if (
    field instanceof HTMLCollection ||
    field instanceof NodeList ||
    Array.isArray(field)
  ) {
    return [...field]
  }

  if (typeof field === 'string') {
    return getFieldById(field, options)
  }
}

export function clearFieldValues (field) {
  const $fields = getField(field, { returnArray: true })
  const changeEvent = new Event('change')

  $fields.forEach(field => {
    const fieldType = field.type
    const xType = field.getAttribute('xtype')

    if (['text', 'textarea', 'select-one', 'hidden'].includes(fieldType)) {
      if (xType === 'FILE') {
        clearFileField(field)
      } else {
        field.value = ''
      }
    } else {
      field.checked = false
    }

    field.dispatchEvent(changeEvent)
  })
}

export function showField (field, container = defaults.container) {
  const $fields = getField(field, { returnArray: true })
  const $container = $fields[0].closest(container)
  const isRequired = hasFieldRequired($fields)

  $container.classList.remove(defaults.hideClass)

  if (isRequired) {
    addRequired($fields, true)
  }
}

export function hideField (field, container = defaults.container) {
  const $fields = getField(field, { returnArray: true })
  const $container = $fields[0].closest(container)
  const isRequired = hasFieldRequired($fields)

  $container.classList.add(defaults.hideClass)

  clearFieldValues($fields)

  if (isRequired) {
    removeRequired($fields, true)
  }
}

export function showTable (table) {
  const $table = getTable(table)
  const $fields = $table.querySelectorAll('[xname]')

  $fields.forEach(field => showField(field))
  $table.classList.remove(defaults.hideClass)
}

export function hideTable (table) {
  const $table = getTable(table)
  const $fields = $table.querySelectorAll('[xname]')

  $fields.forEach(field => hideField(field))
  $table.classList.add(defaults.hideClass)
}

export function onFileChange (field, callback) {
  const $field = getField(field)
  const xType = field.getAttribute('xtype')

  if (xType !== 'FILE') {
    return
  }

  const observer = new MutationObserver(handleFileChange)

  observer.observe($field, { attributes: true })

  function handleFileChange (mutationsList, observer) {
    mutationsList.forEach(mutation => {
      if (mutation.type === 'attributes') {
        const id = $field.getAttribute('xname')
          .substring(3)

        const attachment = field.parentElement
          .querySelector(`#div${id} > a`)

        const deleteBtn = field.parentElement
          .querySelector(`#div${id} > a:last-of-type`)

        const filepath = attachment
          ? attachment.href
          : null

        if (deleteBtn) {
          deleteBtn.addEventListener(
            'click',
            () => callback(null)
          )
        }

        callback(filepath, deleteBtn)
      }
    })
  }
}

/**
 * ðŸ“Œ Form Required
 */

export function addRequired (field, addClass = false) {
  const $fields = getField(field, { returnArray: true })

  $fields.forEach(field => {
    field.setAttribute('required', 'S')
    field.removeAttribute('data-was-required')
  })

  if (addClass && defaults.container === 'tr') {
    const $container = $fields[0].closest(defaults.container)
    $container.classList.add(defaults.requiredClass)
  }
}

export function removeRequired (field, addClass = false) {
  const $fields = getField(field, { returnArray: true })

  $fields.forEach(field => {
    field.setAttribute('required', 'N')
    field.setAttribute('data-was-required', true)
  })

  if (addClass && defaults.container === 'tr') {
    const $container = $fields[0].closest(defaults.container)
    $container.classList.remove(defaults.requiredClass)
  }
}
